import { CircularProgressbar, buildStyles } from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";

interface ReasoningSectionProps {
    title: string;
    percentage: number;
    reasoning: string;
}

const formatPct = (v?: number | null) =>
    `${Math.round(((v ?? 0) as number) * 100)}%`;

const parseReasonings = (raw?: string) => {
    if (!raw) return [];
    return raw
        .split(/[•\n;]+/)
        .map(s => s.trim())
        .filter(Boolean);
};

const ReasoningPill: React.FC<{ text: string; index: number }> = ({ text, index }) => (
    <div
        className={`text-white rounded-md p-3 text-sm text-left shadow-md ${index % 2 === 0 ? "bg-[#B11016]" : "bg-[#800b10]"
            }`}
    >
        {text}
    </div>
);

const ReasoningSection: React.FC<ReasoningSectionProps> = ({
    title,
    percentage,
    reasoning,
}) => {
    const bulletPoints = reasoning
        .split("•")
        .map((point) => point.trim())
        .filter((point) => point.length > 0);

    return (
        <div className="bg-white p-6 rounded-2xl shadow-md flex flex-col items-center w-full md:w-1/3">
            <h3 className="text-xl font-semibold text-[#B11016]">{title}</h3>
            <p className="text-3xl font-bold text-gray-800">{percentage}%</p>
            <hr className="border-t border-gray-300 w-full my-4" />
            <div className="w-full">
                {bulletPoints.map((point, index) => (
                    <div
                        key={index}
                        className={`p-3 rounded-lg mb-2 text-gray-700 text-sm ${index % 2 === 0 ? "bg-red-50" : "bg-red-100"
                            }`}
                    >
                        {point}
                    </div>
                ))}
            </div>
        </div>
    );
};

export default function SynergySection({
    companyName,
    synergyScore,
    credibilityScore,
    referentialScore,
    complianceScore,
    credibilityReasoning,
    referentialReasoning,
    complianceReasoning,
}: {
    companyName: string;
    synergyScore: number;
    credibilityScore: number;
    referentialScore: number;
    complianceScore: number;
    credibilityReasoning: string;
    referentialReasoning: string;
    complianceReasoning: string;
}) {
    // Calculate overallSynergy from synergyScore prop
    const overallSynergy = Math.round((synergyScore ?? 0) * 100);

    return (
        <section className="text-center my-12">
            {/* Company Name */}
            <h2 className="text-3xl sm:text-5xl font-bold text-[#B11016] mb-6">
                {companyName}
            </h2>

            {/* Circular Progress */}
            <div className="w-48 h-48 mx-auto mb-12">
                <CircularProgressbar
                    value={overallSynergy}
                    text={`${overallSynergy}%`}
                    styles={buildStyles({
                        textSize: "20px",
                        textColor: "#111827",
                        pathColor: "#B11016",
                        trailColor: "#d1d5db",
                    })}
                />
            </div>

            {/* Three Cards */}
            <div className="grid grid-cols-1 sm:grid-cols-3 gap-10 max-w-6xl mx-auto text-center">
                {/* Credibility */}
                <div className="bg-white rounded-2xl shadow-md p-6">
                    <h4 className="text-xl font-bold text-[#B11016] mb-2">Credibility</h4>
                    <p className="text-4xl font-bold mb-4">
                        {formatPct(credibilityScore)}
                    </p>
                    <div className="border-t-2 border-gray-800 mb-8"></div>
                    <div className="space-y-3">
                        {parseReasonings(credibilityReasoning).map((t, i) => (
                            <ReasoningPill key={i} text={t} index={i} />
                        ))}
                    </div>
                </div>

                {/* Referential */}
                <div className="bg-white rounded-2xl shadow-md p-6">
                    <h4 className="text-xl font-bold text-[#B11016] mb-2">Referential</h4>
                    <p className="text-4xl font-bold mb-4">
                        {formatPct(referentialScore)}
                    </p>
                    <div className="border-t-2 border-gray-800 mb-8"></div>
                    <div className="space-y-3">
                        {parseReasonings(referentialReasoning).map((t, i) => (
                            <ReasoningPill key={i} text={t} index={i} />
                        ))}
                    </div>
                </div>

                {/* Compliance */}
                <div className="bg-white rounded-2xl shadow-md p-6">
                    <h4 className="text-xl font-bold text-[#B11016] mb-2">Compliance</h4>
                    <p className="text-4xl font-bold mb-4">
                        {formatPct(complianceScore)}
                    </p>
                    <div className="border-t-2 border-gray-800 mb-8"></div>
                    <div className="space-y-3">
                        {parseReasonings(complianceReasoning).map((t, i) => (
                            <ReasoningPill key={i} text={t} index={i} />
                        ))}
                    </div>
                </div>
            </div>
        </section>
    );
}
