"use client";

import { useEffect, useState, Suspense } from "react";
import { useSearchParams, useRouter } from "next/navigation";
import { FaArrowLeft } from "react-icons/fa";
import SynergySection from "@/components/FindCollabCompanySynergy";

export default function BpiFindCollabCompany() {
  return (
    <Suspense fallback={<div>Loading...</div>}>
      <BpiFindCollabCompanyContent />
    </Suspense>
  );
}

function BpiFindCollabCompanyContent() {
  const API = process.env.NEXT_PUBLIC_API_URL;
  const searchParams = useSearchParams();
  const router = useRouter();
  const company = searchParams.get("company");

  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [scoring, setScoring] = useState<{
    credibility_score: number;
    referential_score: number;
    compliance_score: number;
    credibility_reasoning?: string;
    referential_reasoning?: string;
    compliance_reasoning?: string;
  } | null>(null);

  const [projects, setProjects] = useState<
    { title: string; description: string }[]
  >([]);

  useEffect(() => {
    const fetchCompanyData = async () => {
      if (!company) return;

      const token = sessionStorage.getItem("access_token");
      if (!token) {
        setError("Unauthorized. Please log in again.");
        setLoading(false);
        return;
      }

      try {
        setLoading(true);
        const res = await fetch(`${API}/api/find_company/name`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
          body: JSON.stringify({ company }),
        });

        if (!res.ok) throw new Error("Failed to fetch company data");

        const data = await res.json();

        // ✅ Log the raw response for debugging
        console.log("Fetched Company Data:", data);
        const companyData = data.company_name_scrape;
        if (!companyData) throw new Error("Invalid data format");

        // ✅ Set scoring safely
        setScoring({
          credibility_score: Number(companyData.credibility_score) || 0,
          referential_score: Number(companyData.referential_score) || 0,
          compliance_score: Number(companyData.compliance_score) || 0,
          credibility_reasoning: companyData.credibility_reasoning,
          referential_reasoning: companyData.referential_reasoning,
          compliance_reasoning: companyData.compliance_reasoning,
        });

        // ✅ Log computed scoring for clarity
        console.log("Processed Scoring:", {
          credibility_score: Number(data.credibility_score) || 0,
          referential_score: Number(data.referential_score) || 0,
          compliance_score: Number(data.compliance_score) || 0,
        });

        // ✅ Set projects
        const projectList = [
          { title: companyData.project_title1, description: companyData.project_description1 },
          { title: companyData.project_title2, description: companyData.project_description2 },
          { title: companyData.project_title3, description: companyData.project_description3 },
        ];
        setProjects(projectList);

        // ✅ Log processed projects
        console.log("Processed Projects:", projectList);
      } catch (err: any) {
        console.error("Fetch Error:", err);
        setError(err.message || "Something went wrong");
      } finally {
        setLoading(false);
      }
    };

    fetchCompanyData();
  }, [company, API]);


  if (loading) {
    return (
      <div className="p-6 text-center">
        <p className="text-lg font-semibold">Loading company details...</p>
      </div>
    );
  }

  if (error) {
    return (
      <div className="p-6 text-center text-red-600">
        <p>{error}</p>
      </div>
    );
  }

  // ✅ Calculate overall synergy score
  const overallSynergy = scoring
    ? Math.round(
      ((scoring.credibility_score +
        scoring.referential_score +
        scoring.compliance_score) / 3) * 100
    )
    : 0;

  return (
    <div className="max-w-6xl mx-auto p-6">
      <button
        onClick={() => router.back()}
        className="flex items-center mb-4 text-red-600 hover:underline"
      >
        <FaArrowLeft className="mr-2" /> Back
      </button>

      <SynergySection
        companyName={company || ""}
        synergyScore={overallSynergy}
        credibilityScore={scoring ? Math.round(scoring.credibility_score * 100) : 0}
        referentialScore={scoring ? Math.round(scoring.referential_score * 100) : 0}
        complianceScore={scoring ? Math.round(scoring.compliance_score * 100) : 0}
        credibilityReasoning={scoring?.credibility_reasoning}
        referentialReasoning={scoring?.referential_reasoning}
        complianceReasoning={scoring?.compliance_reasoning}
      />

      {/* Projects Section */}
      <section>
        <h2 className="text-xl font-semibold mb-4">Recommended Projects</h2>
        <div className="grid gap-4">
          {projects.map((p, idx) => (
            <ProjectCard key={idx} title={p.title} description={p.description} />
          ))}
        </div>
      </section>
    </div>
  );
}

function CircularProgress({ score }: { score: number }) {
  const radius = 80;
  const circumference = 2 * Math.PI * radius;
  const strokeDasharray = `${(score / 100) * circumference} ${circumference}`;

  return (
    <div className="relative">
      <svg width="200" height="200" className="transform -rotate-90">
        {/* Background circle */}
        <circle
          cx="100"
          cy="100"
          r={radius}
          stroke="#e5e7eb"
          strokeWidth="20"
          fill="none"
        />
        {/* Progress circle */}
        <circle
          cx="100"
          cy="100"
          r={radius}
          stroke="#dc2626"
          strokeWidth="20"
          fill="none"
          strokeDasharray={strokeDasharray}
          strokeLinecap="round"
          className="transition-all duration-1000 ease-out"
        />
      </svg>
      <div className="absolute inset-0 flex items-center justify-center">
        <span className="text-4xl font-bold text-gray-800">{score}%</span>
      </div>
    </div>
  );
}

function ScoreCard({
  title,
  score,
  reasoning,
  color,
}: {
  title: string;
  score?: number;
  reasoning?: string;
  color: string;
}) {
  const percentage = score !== undefined ? (score * 100).toFixed(0) : "0";

  return (
    <div className="bg-white rounded-lg shadow-lg overflow-hidden">
      <div className="text-center py-4 border-b-2 border-gray-200">
        <h3 className="font-bold text-lg text-gray-800">{title}</h3>
        <p className="text-4xl font-bold text-gray-800 mt-2">{percentage}%</p>
      </div>

      <div className={`${color} text-white p-4 space-y-4`}>
        {reasoning && (
          <details className="mt-4">
            <summary className="font-semibold text-sm cursor-pointer hover:text-gray-200">
              View Detailed Analysis
            </summary>
            <p className="mt-2 text-xs leading-relaxed whitespace-pre-line border-t border-red-400 pt-2">
              {reasoning}
            </p>
          </details>
        )}
      </div>
    </div>
  );
}

function ProjectCard({ title, description }: { title: string; description: string }) {
  return (
    <div className="bg-white p-4 rounded-lg shadow">
      <h3 className="text-lg font-semibold mb-2">{title}</h3>
      <p className="text-gray-700">{description}</p>
    </div>
  );
}
